import React from 'react';
import * as PropTypes from 'prop-types';
import withStyle from './withStyle';

const Footer = ({ className }) => (
  <footer className={`${className}`}>
    <div className="footer-column">
      <h4>Airbnb</h4>
      <div className="link">Carrires</div>
      <div className="link">Presse</div>
      <div className="link">Rgles</div>
      <div className="link">Aide</div>
      <div className="link">Diversit et intgration</div>
      <div className="link">Coordonnes de l'entreprise</div>
    </div>
    <div className="footer-column">
      <h4>Dcouvrir</h4>
      <div className="link">Confiance et scurit</div>
      <div className="link">Crdit voyage</div>
      <div className="link">Airbnb Citizen</div>
      <div className="link">Voyage d'affaires</div>
      <div className="link">Guides</div>
      <div className="link">Airbnbmag</div>
      <div className="link">vnements</div>
    </div>
    <div className="footer-column">
      <h4>Hbergement</h4>
      <div className="link">Pourquoi tre hte</div>
      <div className="link">Hospitalit</div>
      <div className="link">Hbergement responsable</div>
      <div className="link">Community Center</div>
      <div className="link">Organisez une exprienceNouveau</div>
      <div className="link">Open Homes</div>
    </div>
    <div className="footer-column">
      <div className="social-icons">
        <span className="icon fb-icon" />
        <span className="icon twitter-icon" />
        <span className="icon insta-icon" />
      </div>
      <div className="link">Conditions</div>
      <div className="link">Politique de confidentialit</div>
      <div className="link">Plan du site</div>
    </div>
    <div className="clear-fix" />
    <div className="footer-copyright">
      <div className="copyright-text"> Airbnb, Inc.</div>
    </div>
  </footer>
);

Footer.propTypes = {
  className: PropTypes.string.isRequired,
};

export default withStyle(Footer);
